@import '../styles/App.scss';
//About
.about-section {
  width: 100%;
  padding: 4rem 0;
  h5 {
      margin: 0;
      font-size: 2.5rem;
      letter-spacing: 1px;
      font-weight: 400;
      margin-bottom: 2rem;
  }
  p{
      // font-size: 20px;
      // line-height: 45px;
      // letter-spacing: 1px;
      // font-weight: 300;
      // width: auto;
      font-size: 18px;
      letter-spacing: 0.5px;
      line-height: 30px;
      font-weight: 300;
      line-height: 2.28rem;
      padding-bottom: 2rem;
  }
  .banner {
    width: 100%;
    position: relative;
    margin-top: 2rem;
  }
  .image-container {
    translate: none;
    rotate: none;
    scale: none;
    max-width: 1280px;
    margin: 0 auto;
    transform: translate(0%, 2.9942%) translate3d(0px, -28.3587px, 0px);
    .img-frame {
      width: 100%;
      padding-bottom: 66.5714%;
    }
      img {
        display: block;
        max-width: 100%;
        height: auto;
      }
    }
  }
  
  


@media (min-width: $layout-breakpoint-small) {
  .about-section {
    p{
      font-size: 22px;
      line-height: 53px;
      text-align: left;
      margin-top: 1rem;
    }
    h5{
      margin-right: 5rem;
      font-size: 2.5rem;
    }
    .about-wrap {
      margin: 0 auto;
      display: flex;
      -webkit-box-align: start;
      -ms-flex-align: start;
      align-items: flex-start;
      }
  }
}

@media (min-width: $layout-breakpoint-medium) {
  
  .about-section {
    p{
      font-size: 24px;
      line-height: 60px;
      letter-spacing: 0px;
    }
    h5{
      margin-right: 25rem;
      
    }
  }
}
  