@import '../styles/App.scss';

.project {
  width: 90%;
  margin: 0 auto;
  padding: 5rem 0;
  max-width: 1520px;
  h4 {
    text-transform: uppercase;
    font-size: 12px;
    line-height: 2em;
    letter-spacing: 4px;
    font-weight: 500;
    color: $grey;
    margin-bottom: 2.5em;
  }
  h2 {
    font-size: 42px;
    font-weight: 500;
    line-height: 1.3em;
    letter-spacing: 1.5px;
    // color: $black;
    mix-blend-mode: difference;
    margin: 0;
  }
  p {
    font-size: 18px;
    letter-spacing: .5px;
    line-height: 30px;
    font-weight: 300;
    line-height: 2.28rem;
    padding-bottom: 2rem;
  }
  .hero-container {
    width: 100%;
    margin-top: 5rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 600px;
    position: relative;
  }
  .hero-content-inner {
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 600px;
    position: relative;
  }
  .link {
    font-size: 1rem;
    color: $black;
    text-decoration: underline;
    font-weight: 500;
  }
  .content {
    margin-top: 2rem;
  }
  .img-container {
    position: absolute;
    margin-top: 0;
    height: 100%;
    width: 52vw;
    max-width: 900px;
    right: 0;
    top: 0%;
    .img-frame {
      position: relative;
      overflow: hidden;
      width: 100%;
      padding-bottom: 66.5714%;
    }
    img {
      position: absolute;
      top: 0px;
      left: 0px;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center center;
    }
  }
}

@media (min-width: $layout-breakpoint-small){
  .project .img-container {
    position: absolute;
    margin-top: 0;
    height: 100%;
    width: 52vw;
    max-width: 900px;
    right: 0;
    top: -100%;
  }
}